@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: lightblue;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
}

body {
  position: fixed;
  overflow: hidden;
  overscroll-behavior-y: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-size: 40px;
}

.flex {
  display: flex;
}

.welcome {
  display: flex;
  flex-direction: column;
  background-color: black;
  top: 0%;
  color: white;
  height: 100%;
  align-items: center;
  justify-content: center;
  transition: all;

  animation-duration: 0.5s;
}

.welcomeTextleft {
  display: block;

  text-align: left;
  margin-left: 10px;
  margin-right: 10px;
  font-size: x-large;
  margin-bottom: 20px;
}
.welcomeText1 {
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  font-size: x-large;
}

.welcomeTextresponse {
  text-align: center;
  margin-top: 20px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: x-large;
}

.welcomeText2 {
  text-align: right;

  margin-left: 10px;
  margin-right: 10px;
  font-size: large;
}
.welcomeText:hover {
  text-decoration: underline;
  cursor: pointer;
}
.fixed {
  position: fixed;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.centered {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.cursor {
  color: white;
}

.texture-selector {
  transform: scale(5);
}

.texture-selector img.active {
  border: 2px solid red;
}

.contenedormenu {
  display: flex;
  top: 0px;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.menu {
  top: 0px;
  width: 1000px;
  background-color: rgb(255, 255, 255, 0.8);
  height: 800px;
  border-radius: 25px;
  margin: 4px;

  text-align: center;
}

.contenedormenutext {
  padding-left: 10%;
  padding-right: 10%;
}
.menutext {
  font-size: 20px;
}

.menutext2 {
  font-size: 17px;
}

.logoContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}

.rotate-center {
  -webkit-animation: rotate-center 8s linear infinite;
  animation: rotate-center 8s linear infinite;

  margin-left: 20px;
  margin-right: 20px;
}

.intructionscontainer {
}

.keyboardintructions {
  margin-top: 15px;
  font-size: 20px;

  flex: auto;
  align-items: center;
}

.keyboardcontainer {
  margin: auto;
  border: 3px solid black;
  width: 50px;

  border-radius: 10px;
}
.asd {
  display: flex;
  align-items: center;
  width: fit-content;

  margin: auto;
  align-items: center;
}

.clickToStart {
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}
@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

@-webkit-keyframes rotate-center {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotate-center {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
